import React from 'react';
import Slider from "react-slick";
import ApiProvider from "../providers/api_provider";
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'
import Vacancy from "./vacancy";

class Vacancies extends React.Component {
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }

    settingsSlider = {
        dots: true,
        infinite: false,
        speed: 500,
        arrows: true,
        slidesToShow: 3,
        slidesToScroll: 1,
        onInit: () => {
            this.setState({
                sliderInit: true,
            })
        },
        beforeChange: (current, next) => {
            this.changeSliderIndex(current, next);
        },
    };

    state = {
        pageLoading: true,
        items: [],
        sliderInit: false,
    }

    loadData = async () => {
        const items = await ApiProvider.getVacancies();
        this.setState({
            pageLoading: false,
            items: items,
        });
    }

    componentDidMount() {
        this.loadData();
    }

    next() {
        if (this.slider !== undefined) {
            this.slider.slickNext();
        }
    }

    previous() {
        if (this.slider !== undefined) {
            this.slider.slickPrev();
        }
    }

    changeSliderIndex = (current, next) => {
        if (current === 0 && next !== 0) {
            document.querySelector('.vacancy-prev-arrow').classList.remove('disabled');
        } else if (current !== 0 && next === 0) {
            document.querySelector('.vacancy-prev-arrow').classList.add('disabled');
        }
        if ((next + this.slider.props.slidesToShow) === this.state.items.length) {
            document.querySelector('.vacancy-next-arrow').classList.add('disabled');
        } else {
            document.querySelector('.vacancy-next-arrow').classList.remove('disabled');
        }
    }

    render() {
        return (
            <div className="vacancy__wrapper">
                <div className="wrapper">
                    <div className="vacancy__title-wrapper">
                        <h2 className="vacancy__title h2">вакансии в гросс маркете</h2>
                        {this.state.sliderInit && <div className="vacancy__slider-arrows">
                            <button className="vacancy__slider-arrow vacancy-prev-arrow disabled"
                                    onClick={this.previous}/>
                            <button className="vacancy__slider-arrow vacancy-next-arrow" onClick={this.next}/>
                        </div>}
                    </div>
                    <div className="vacancy__list">
                        {this.state.pageLoading && this.renderLoaders()}
                        {(!this.state.pageLoading && this.state.items.length > 0) && this.renderVacancyItems()}
                    </div>
                </div>
            </div>
        )
    }

    renderVacancyItems = () => {
        const items = [];
        for (const item of this.state.items) {
            items.push(<Vacancy vacancy={item}/>)
        }

        return (
            <Slider ref={c => (this.slider = c)} {...this.settingsSlider}>
                {items}
            </Slider>
        )
    }

    renderLoaders = () => {
        return (
            <Slider {...this.settingsSlider}>
                <div className="vacancy__list-item vacancy__list-item__loader">
                    <div/>
                </div>
                <div className="vacancy__list-item vacancy__list-item__loader">
                    <div/>
                </div>
                <div className="vacancy__list-item vacancy__list-item__loader">
                    <div/>
                </div>
                <div className="vacancy__list-item vacancy__list-item__loader">
                    <div/>
                </div>
                <div className="vacancy__list-item vacancy__list-item__loader">
                    <div/>
                </div>
                <div className="vacancy__list-item vacancy__list-item__loader">
                    <div/>
                </div>
            </Slider>
        )
    }
}

export default Vacancies;