import React from 'react';
import ApiProvider from "../providers/api_provider";
import {Placemark} from "react-yandex-maps";

class InstagramFeed extends React.Component {
    vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0)
    firstPageSize = this.vw >= 1440 ? 6 : 5;
    pageSize = this.vw >= 1440 ? 8 : 6;

    state = {
        page: 0,
        pageLoading: true,
        totalCnt: 0,
        photos: [],
    }

    loadData = async (pageSize = 0) => {
        this.setState({
            pageLoading: true,
        });
        await new Promise(resolve => setTimeout(resolve, 5000));
        const _photos = this.state.photos;
        _photos.push('');
        _photos.push('');
        _photos.push('');
        _photos.push('');
        _photos.push('');

        this.setState({
            // pageLoading: false,
            photos: _photos,
        });
    }

    componentDidMount() {
        this.loadData(this.firstPageSize);
    }

    render() {
        return (
            <div className="instagram-feed__wrapper">
                <div className="wrapper">
                    <h2 className="instagram-feed__title h2">мы в инстаграме</h2>
                    <div className="instagram-feed__items">

                    </div>
                    {this.renderLoader()}
                </div>
            </div>
        )
    }

    renderLoader = () => {
        const loaders = [];
        for (let i = 0; i < (this.state.page === 0 ? this.firstPageSize : this.pageSize); i++) {
            loaders.push(<div className="instagram-feed__items__loader"/>);
        }
        return (
            <div className="instagram-feed__items__loader-list">{loaders}</div>
        )
    }
}

export default InstagramFeed;