import React from 'react';
import { YMaps, Map, Placemark, ZoomControl } from 'react-yandex-maps';
import ApiProvider from "../providers/api_provider";

class Geography extends React.Component {
    state = {
        placemarks: [],
        placemarksFiltered: [],
        placemarkSelectedType: 0,
    }

    loadPlaceMark = async (type = 0) => {
        if (this.state.placemarks.length === 0) {
            const placemarkOptions = {
                iconLayout: 'default#image',
                iconImageHref: '/_images/map_placemark.svg',
                iconImageSize: [44, 44],
                iconImageOffset: [-22, -22]
            };
            let _placemarks = [], _placemarksFiltered = [];
            const points = await ApiProvider.getMapPoints();
            for (const point of points) {
                const coordSplit = point.PROPERTY_COORD_VALUE.replace(/\s/g, '').split(',');
                const _placemark = <Placemark geometry={[coordSplit[0], coordSplit[1]]} options={placemarkOptions}/>;

                _placemarksFiltered.push(_placemark);
                _placemarks.push({
                    placemark: _placemark,
                    type: point.PROPERTY_TYPE_ENUM_ID,
                });
            }
            this.setState({
                placemarks: _placemarks,
                placemarksFiltered: _placemarksFiltered,
            })
        } else {
            const _placemarks = [];
            for (var point of this.state.placemarks) {
                if (type === 0 || point.type === `${type}`) _placemarks.push(point.placemark);
            }
            this.setState({
                placemarksFiltered: _placemarks,
                placemarkSelectedType: type,
            })
        }
    }

    componentDidMount() {
        this.loadPlaceMark();
    }

    render() {
        return (
            <div className="geography__wrapper">
                <div className="wrapper">
                    <h2 className="geography__title h2">география</h2>
                    <div className="geography__map">
                        <YMaps>
                            <Map defaultState={{ center: [55.755819, 37.617644], zoom: 12 }} width="100%" height="100%">
                                {this.state.placemarksFiltered}
                                <ZoomControl options={{ size: 'small', position: {bottom: 24, right: 24} }} />
                            </Map>
                        </YMaps>
                        <div className="geography__map-filter">
                            <button className={`geography__map-filter__button${this.state.placemarkSelectedType === 6 ? ' active' : ''}`} onClick={() => this.loadPlaceMark(6)}>юрлица</button>
                            <button className={`geography__map-filter__button${this.state.placemarkSelectedType === 5 ? ' active' : ''}`} onClick={() => this.loadPlaceMark(5)}>физлица</button>
                            <button className={`geography__map-filter__button${this.state.placemarkSelectedType === 0 ? ' active' : ''}`} onClick={() => this.loadPlaceMark(0)}>показать всё</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Geography;