import React from 'react';
import Slider from "react-slick";
import ApiProvider from "../providers/api_provider";
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'

const NextArrow = props => {
    const { className, style, onClick } = props;
    return (
        <button
            className={className}
            style={{ ...style, display: "block", background: "red" }}
            onClick={onClick}
        />
    );
}

const PrevArrow = props => {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{ ...style, display: "block", background: "green" }}
            onClick={onClick}
        />
    );
}

class Banners extends React.Component {
    settingsSlider = {
        dots: false,
        infinite: false,
        speed: 500,
        arrows: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        nextArrow: <NextArrow />,
        prevArrow: <PrevArrow />
    };

    state = {
        pageLoading: true,
        banners: [],
        sliderInit: false,
    }

    loadData = async () => {
        const banners = await ApiProvider.getBanners();
        this.setState({
            pageLoading: false,
            banners: banners,
        });
    }

    componentDidMount() {
        this.loadData();
    }

    render() {
        return (
            <div className="banner__wrapper">
                {this.state.pageLoading && this.renderLoaders()}
                {(!this.state.pageLoading && this.state.banners.length > 0) && this.renderBanners()}
            </div>
        )
    }

    renderBanner = (banner) => {
        return (
            <div className="banner__item">
                <div className="banner__item-bg">
                    <img src={banner.file} alt={banner.name} />
                </div>
                <div className="banner__item-content">
                    <div className="wrapper">
                        <div className="banner__item-name h1">{banner.name}</div>
                    </div>
                </div>
            </div>
        )
    }

    renderBanners = () => {
        const banners = [];
        for (const banner of this.state.banners) {
            banners.push(this.renderBanner(banner));
        }

        return (
            <Slider {...this.settingsSlider}>
                {banners}
            </Slider>
        )
    }

    renderLoaders = () => {
        return (
            <Slider {...this.settingsSlider}>
                <div className="banner__item-stub"/>
                <div className="banner__item-stub"/>
                <div className="banner__item-stub"/>
            </Slider>
        )
    }
}

export default Banners;